.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ramecek {
  position: relative;
  display: inline-block;
  color: white;
  padding: 11px 40px;
  white-space: nowrap;
  z-index: 10;
}
.ramecek:before {
  content: "";
  position: absolute;
  inset: 0;
  background: #8900c9;
  clip-path: polygon(0 26.00px,26.00px 0,100% 0,100% calc(100% - 26.00px),calc(100% - 26.00px) 100%,0 100%,0 26.00px,5px  calc(26.00px + 2.07px),5px calc(100% - 5px),calc(100% - 26.00px - 2.07px) calc(100% - 5px),calc(100% - 5px) calc(100% - 26.00px - 2.07px),calc(100% - 5px) 5px,calc(26.00px + 2.07px) 5px,5px calc(26.00px + 2.07px));
  z-index: -1;
}
.ramecek:after {
  content: "";
  position: absolute;
  inset: 4px;
  background: #24003b;
  clip-path: polygon(0 24.00px,24.00px 0,100% 0,100% calc(100% - 24.00px),calc(100% - 24.00px) 100%,0 100%);
  z-index: -1;
}
